import { RE_USERNAME, RE_PASSWORD } from 'utils/regex'

export default {
  username: {
    label: '',
    icon: 'iconfont-user-o',
    type: 'textfield',
    attrs: {
      placeholder: RE_USERNAME[1]
    },
    validate: {
      required: {
        rule: true,
        message: ''
      },
      minlength: {
        rule: 4,
        message: ' 4 '
      },
      maxlength: {
        rule: 20,
        message: ' 20 '
      },
      pattern: {
        rule: `/${RE_USERNAME[0].source}/`,
        message: ''
      }
    }
  },
  password: {
    label: '',
    icon: 'iconfont-lock-o',
    type: 'password',
    attrs: {
      placeholder: RE_PASSWORD[1]
    },
    validate: {
      required: {
        rule: true,
        message: ''
      },
      minlength: {
        rule: 8,
        message: ' 8 '
      },
      maxlength: {
        rule: 20,
        message: ' 20 '
      },
      pattern: {
        rule: `/${RE_PASSWORD[0].source}/`,
        message: ''
      }
    }
  }
}



